import { Button, Form, Input, InputNumber, message, Modal, Upload } from "antd";
import { useEffect } from "react";
import { Course } from "./Course";
import axios from "axios";

export function Demo3Insert({
  open,
  course,
  onSuccess,
  onCancel,
}: {
  open: boolean,
  course: Course,
  onSuccess?:()=> void,
  onCancel?:()=> void
}) {
  const [form] = Form.useForm();
  async function onOk() {
    try {
      const values = await form.validateFields();
      //修改数据库
      const resp = await axios.post(`http://localhost:8080/insert`,values,{headers:{ 'Content-Type': 'multipart/form-data'}})
      message.success("修改成功");
      onSuccess && onSuccess();
    } catch (e) {
      console.log(e)
      message.error("错误");
    }
  }
  useEffect(() => {
    form.setFieldsValue(course);
  }, [course]);
  return (
    <Modal open={open} title="新增学生" onOk={onOk} onCancel={onCancel}>
      <Form form={form} >
        <Form.Item label="课程名" name={"name"}>
          <Input></Input>
        </Form.Item>
        <Form.Item label="课时" name={"hours"}>
          <InputNumber></InputNumber>
        </Form.Item>
        <Form.Item label="学院" name={'college'}>
          <Input></Input>
        </Form.Item>
        <Form.Item
          label="封面图片"   
          name="file"
        >
          <Upload
            name="image"
            beforeUpload={() => {
              return false;
            }}
            withCredentials={true}
            listType="picture"
          >
            <Button >上传</Button>
          </Upload>
        </Form.Item> 
      </Form>
    </Modal>
  );
}
